import {request} from './request'

//获得计划单
export async function GetAllMultiSizePlans(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/GetAllMultiSizePlans',
        data:data
    })
}

//新增或更新计划单
export async function InsertOrUpdateMultiSizePlan(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/InsertOrUpdateMultiSizePlan',
        data:data
    })
}

//获得单个计划表信息
export async function GetSelMultiSizePlans(data){
    return await request({
        url:'/api/BusinessUnit/GetSelMultiSizePlans',
        params:{planId:data}
    })
}

//删除计划单
export async function DeleteMultiSizePlan(data){
    return await request({
        method:'DELETE',
        url:'/api/BusinessUnit/DeleteMultiSizePlan',
        data:data
    })
}

//单个计划表删除
export async function DeleteMultiSizePlanDetails(data){
    return await request({
        method:'DELETE',
        url:'/api/BusinessUnit/DeleteMultiSizePlanDetails',
        data:data
    })
}

//批量更新状态
export async function UpdateMultiSizePlanState(data){
    return await request({
        method:'PUT',
        url:'/api/BusinessUnit/UpdateMultiSizePlanState',
        data:data
    })
}