export function dateFilter(date){
    dateReload();
    return date.format();
}
function dateReload(){
    Date.prototype.format = function () {
        var o = {
          y: this.getFullYear(),
          mo: this.getMonth()+1,
          d: this.getDate(),
          h: this.getHours(),
          mi: this.getMinutes(),
        };
        return "" + o.y + "-" + o.mo + "-" + o.d /* + " " + o.h + "-" + o.mi */;
      };
}
export function arraySort(data){
  data.sort((val1, val2) => {
    if (val1 < val2) {
      return 1;
    } else {
      return -1;
    }
  });
  return data;
}

export function addIndex(data){
  data.forEach((item, index) => {
    item.index = index;
  });
}