<script>
import {
  GetSelMultiSizePlans,
  InsertOrUpdateMultiSizePlan,
  DeleteMultiSizePlanDetails,
} from "network/newMulti.js";
import { arraySort } from "service/dateUtil.js";

import { TableItem, FlowerItem , FlowerSubItem , DeliverItem } from "service/Business/newMulti.js";

export default {
  data() {
    return {
      formData:{
        planId:0,
        orderId:"",
        creationTime: "",
        produceGroup: "",
        salesManId: 0,
        salesManName: "",
        customerId: 0,
        customerName: "",
        reviewer: "",
        plan_Details:[],
        sampleConfirmTime: "",
        rawMateriaCalcTime: "",
        rawMateriaPrepareTime: "",
        samplePreProduceTime: "",
        produceTime: "",
        sewingTime: "",
        packingTime: "",
        comment3: "",
        comment4: "",
        planState: "完成",
      },
      tableData: [
      ],
      search: "",
      emptyItem:null,
      rowSelect: 0,
      selectData:[],
      row: {},
      sizeMax:7,
      deliverMax:1,
      items: [
        { label: "66", width: "120", prop: "number", isTableCell: true },
        { label: "80", width: "120", prop: "number", isTableCell: true },
        { label: "100", width: "120", prop: "number", isTableCell: true },
        { label: "120", width: "120", prop: "number", isTableCell: true },
        { label: "140", width: "120", prop: "number", isTableCell: true },
        { label: "160", width: "120", prop: "number", isTableCell: true },
        { label: "170", width: "120", prop: "number", isTableCell: true }
      ],
      footerItems: [
        { label: "样品确认", prop: "sampleConfirmTime" },
        { label: "大货算料", prop: "rawMateriaCalcTime" },
        { label: "大货备料", prop: "rawMateriaPrepareTime" },
        { label: "产前样", prop: "samplePreProduceTime" },
        { label: "生产", prop: "produceTime" },
        { label: "缝头、定型", prop: "sewingTime" },
        { label: "检品包装", prop: "packingTime" },
      ],
      deleteData: null,
      removeDisplay: false,
      submitDialogDisplay: false,
      checked: false,
    };
  },
  created() {
    this.inital();
  },
  methods: {
    //初始化
    inital() {
      if (this.$route.query.iid == undefined) {
      } else {
        let data = this.$route.query.iid;
        GetSelMultiSizePlans(data).then((res) => {
          if (res.success) {
            this.formData = res.result;
            this.tableDataInital(this.formData.plan_Details);
          } else {
            this.$message.error("发送请求失败！")
          }
        });
      }
    },
    //添加数据编号
    addIndex(data) {
      data.forEach((item, index) => {
        item.index = index;
      });
    },
    //得到缺少的尺寸项
    getSize(data){
      switch (data){
        case 1:
          return "80";
        case 2:
          return "100";
        case 3:
          return "120";
        case 4:
          return "140";
        case 5:
          return "160";
        case 6:
          return "170";
      }
    },
    //尺寸数据预处理
    //根据确认的尺寸项添加尺寸数据
    preSizeData(data) {
      data.forEach((item) => {
        item.patternDescriptions.forEach((subItem)=>{
          let i = subItem.plan_MultiSize_SizeAmounts.length;
          for (i; i < this.sizeMax; i++) {
          const size = this.getSize(i);
          console.log(size);
          this.emptyItem = new FlowerSubItem(size);
          subItem.plan_MultiSize_SizeAmounts.push(this.emptyItem);
        }
        })
      });
    },
    //得到交期最大值
    getDeliverMax(data){
      data.forEach((item) => {
        let len = item.deliveryTimes.length;
        /* console.log("12121"); */
        this.deliverMax = len > this.deliverMax ? len : this.deliverMax;
      });
    },
    //交期数据预处理
    preDeliverData(data){
      data.forEach(item=>{
        let i = item.deliveryTimes.length;
        for (i; i < this.deliverMax; i++) {
          let empty = new DeliverItem();
          item.deliveryTimes.push(empty);
        }
      })
    },
    //数据初始化
    tableDataInital(data) {
      this.addIndex(data);
      this.getDeliverMax(data);
      this.preSizeData(data);
      this.preDeliverData(data);
    },
    //筛选空数据
    filterData(){
      let data = JSON.parse(JSON.stringify(this.formData.plan_Details));
      data.forEach(item=>{
        item.deliveryTimes = item.deliveryTimes.filter(item1 =>{
          if(item1.deliveryTime!=""||item1.ratioInPercent!=""){
            return true;
          }
          else{
            return false;
          }
        })
        item.patternDescriptions.forEach(patternItem=>{
          patternItem.plan_MultiSize_SizeAmounts = patternItem.plan_MultiSize_SizeAmounts.filter(sizeItem=>{
            if(sizeItem.amount===""){
              return false
            }
            else{
              return true;
            }
          })
        })
      })
      return data
    },
    //删除按钮
    remove(){
      const data =this.selectData;
      //判断是否为空
      if(data.length==0){
        this.$message.error("请先选择删除项");
      }
      else{
        let arr = new Array(data.length);
        let arr1 = new Array(data.length);
        data.forEach((item,index)=>{
          arr[index] = item.planDetailId;
          arr1[index] = item.index;
        });
        arr1 = arraySort(arr1);
        //对界面数据进行删除
        arr1.forEach((item)=>{
           this.formData.plan_Details.splice(item, 1);
        })
        this.deleteData = arr;
      }
    },
    //删除操作
    delete(){
      DeleteMultiSizePlanDetails(this.deleteData).then((res) => {
        if (res.success) {
          this.$message.success("删除成功");
        }
      });
    },
    // 获得选中行数据 
    rowClick(selection) {
      this.selectData = selection;
    },
    //插入一整行
    insertPo(){
      let data = this.formData.plan_Details;
      let tableItem = new TableItem();
      let flowerItem = new FlowerItem();
      tableItem.patternDescriptions.push(flowerItem);
      data.push(tableItem);
      this.addIndex(data);
      this.preSizeData(data);
      this.preDeliverData(data);
    },
    //插入一个花型
    insertFlower(){
      if(this.selectData.length==0){
        this.$message.error("请先选择项！");
      }
      else{
        let flowerItem = new FlowerItem();
        this.selectData[0].patternDescriptions.push(flowerItem);
      }
    },
    //增加交期
    insertDelver(){
      this.deliverMax+=1;
      this.preDeliverData(this.formData.plan_Details);
    },
    //取消操作
    cancel() {
      this.$router.go(-1);
    },
    //提交操作
    submit(){
      if(this.deleteData){
        this.delete();
      }
      else{
      }
      this.formData.plan_Details = this.filterData();
      InsertOrUpdateMultiSizePlan(this.formData).then(res=>{
        if(res.success){
          this.$message.success("提交成功");
        }
      })
    },
    //
    selectMergeRow(selection, row) {
      console.log(selection);
      console.log(row);
    },
  },
};
</script>

